<template>
  <div id="leftNav" :style="{'width':$store.state.leftNav.width}">
    <div class="route clear-fix">
      <el-col :span="24" class="navContent">
        <el-menu default-active="1" class="el-menu-vertical-demo" @open="handleOpen" @close="handleClose" style="height:100%;">
          <el-menu-item index="1" @click="goPreview"><i class="el-icon-message"></i><span :class="{hidden:$store.state.leftNav.hidden}">我的数据</span></el-menu-item>
          <el-menu-item index="2"><i class="el-icon-menu"></i><span :class="{hidden:$store.state.leftNav.hidden}">导航二</span></el-menu-item>
          <el-menu-item index="3"><i class="el-icon-setting"></i><span :class="{hidden:$store.state.leftNav.hidden}">导航三</span></el-menu-item>
        </el-menu>
      </el-col>
    </div>
    <div class="toggleBar" @click="toggleNav">
      <i class='el-icon-arrow-left'></i>
    </div>
  </div>
</template>
<script>
  export default {
    methods: {
      mounted(){
        console.log(this.$store.state.leftNav.width);
      },
      goPreview(){
        this.$router.push({name:'dataPreview'});
      },
      handleOpen(key, keyPath) {
        console.log(key, keyPath);
      },
      handleClose(key, keyPath) {
        console.log(key, keyPath);
      },
      toggleNav(){
        this.$store.dispatch(this.$store.state.leftNav.hidden==true?'set_nav_open':'set_nav_close');
       }
    }
  }
</script>
<style scoped lang="scss">
  #leftNav{
    position:fixed;
    // width:190px;
    height:100%;
    left:0;
    transition:all 0.2s ease-in;
    // top:60px;
    // margin-top: 60px;
    background-color:#eef1f6;
    .route{}
    span{margin-left: 30px;}
    .toggleBar{
      width: 100%;
      height: 50px;
      background: darken(#eef1f6,5%);
      position: absolute;
      bottom: 50px;
      left: 0px;
      z-index: 1000;
      cursor: pointer;
      line-height: 40px;
      text-align: center;
      color: black;
      font-size: 14px;
    }
  }
  .hidden {
    display:none;
  }
</style>



